'use strict';

var createParentStream = require('iframe-stream').ParentStream;
var SwController = require('sw-controller');
var SwStream = require('sw-stream/lib/sw-stream.js');

var keepAliveDelay = Math.floor(Math.random() * (30000 - 1000)) + 1000;
var background = new SwController({
  fileName: './scripts/background.js',
  keepAlive: true,
  keepAliveInterval: 30000,
  keepAliveDelay: keepAliveDelay
});

var pageStream = createParentStream();
background.on('ready', function () {
  var swStream = SwStream({
    serviceWorker: background.controller,
    context: 'dapp'
  });
  pageStream.pipe(swStream).pipe(pageStream);
});
